.background {
	height:100%;
	background: #FC466B;
	background: -webkit-linear-gradient(to right, #3F5EFB, #FC466B);
	background: linear-gradient(to right, #3F5EFB, #FC466B);
}

#about-me, #contact-me h3 {
	text-align: center;
}

.avatar-img {
	object-fit: cover;
	width: 225px;
	height: 225px;
	border-radius: 50%;
	/* margin-top: 5em; */
}

.banner h1, .banner h3, .banner p {
	color: white;
}

.banner {
	background-color: black;
	opacity: .7;
	border-radius: 1em;
	margin-left: 1em;
	margin-right: 1em;
}

.banner h3 {
	padding-top: 0.7em;
	margin: auto;
}

.banner h1 {
	font-size: 56px;
	font-weight: bold;
	padding: 5px 0px;
	margin-bottom: 0;
}

a {
	text-decoration: none;
}

.banner>p {
	font-size: 20px;
	padding: 0em 1em 1em
}

.banner i {
	color: white;
}

.social-links {
	text-align: left;
	margin: auto;
}

#contact-me {
	margin-top: 1em;
	margin-bottom: 1em;
}

.social-links table {
	border-spacing: 0 16px;
	padding-left: 1em;
	padding-right: 1em;
}

.social-links i {
	color: white;
	font-size: 4em;
	padding: 8px;
}

.social-links p {
	font-size: 20px;
	color: white;
	margin: auto;
	padding-left: 1em;
	padding-right: 1em;
}

#content{
	overflow-y: visible;
	overflow-x: visible;
}
